












































































































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
import { Prop, Watch } from "vue-property-decorator";

import Wintabe from "../../components/Wintabe.vue";
import Winbeet from "../../components/Winbeet.vue";
@Component({
  components: {
    Wintabe,
    Winbeet
  },
  mixins: [mixin]
})
export default class Cart extends Vue {
  get totalPrice() {
    var totalMoney = 0;
    for (var i = 0; i < this.cartList.length; i++) {
      if (this.result.indexOf(this.cartList[i].id) !== -1) {
        totalMoney = totalMoney + this.cartList[i].price * this.cartList[i].num;
      }
    }
    totalMoney = totalMoney;
    totalMoney.toString();
    return totalMoney.toFixed(2);
  }
  get cartList() {
    let a: any = this.$refs.wintabe;
    if (this.tavkend) {
      return a.cartList;
    } else {
      return "";
    }
  }


   onSubmit() {
    if (this.result.length <= 0) {
      Toast("您还没选择商品");
      return;
    }

    Vue.prototype.$reqFormPost1(
      "/prepare/order/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartIdList: this.result.join(",")
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log("网络请求错误！");
          return;
        }
        this.$store.commit(Vue.prototype.MutationTreeType.PREPAREID,res.data.prepareId)
        this.$router.push({
          path: "/settle"
        });
      }
    );
  }


  num = 1;
  tavkend = false;
  changeStatus() {
    this.tavkend = true;
  }
  allSelect() {
    if (this.checked) {
      let result = [];
      this.cartList.forEach((item, index) => {
        console.log();
        result.push(item.id);
      });
      this.result = result;
    } else if (this.cartList.length == this.result.length) {
      this.result = [];
    }
  }
  result = [];
  checked = false;
  checkchange() {
    if (this.result.length == this.cartList.length) {
      this.checked = true;
    } else {
      this.checked = false;
    }
  }
  deleteCart(index, collect) {
    Vue.prototype.$reqFormPost1(
      "/shop/cart/delete",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartIds: this.cartList[index].id
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log("网络请求错误！");
          return;
        }

        if (!collect) {
          this["$Message"].success("已删除");
        }

        let a: any = this.$refs.wintabe;
        a.getCartList();
      }
    );
  }
  collect(index) {
    Vue.prototype.$reqFormPost1(
      "/fav/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        goodsId: this.cartList[index].goodsId
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log("网络请求错误！");
          return;
        }
        this.deleteCart(index, true);
        this["$Message"].success("已移至收藏夹");
      }
    );
  }

  pluscart(id, num) {
    Vue.prototype.$reqFormPost1(
      "/shop/cart/updatenum",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartId: id,
        num: num
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log("网络请求错误！");
          return;
        }

        let a: any = this.$refs.wintabe;
        a.getCartList();
      }
    );
  }
  minuscart(id, num) {
    Vue.prototype.$reqFormPost1(
      "/shop/cart/updatenum",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartId: id,
        num: num
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log("网络请求错误！");
          return;
        }

        let a: any = this.$refs.wintabe;
        a.getCartList();
      }
    );
  }
  mounted() {
    if (
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].userId !=
        "" &&
      this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].token != ""
    ) {
    }
  }
}



